import type { BiPluginConfig } from '@ali/4ever-cangjie';
import { biFactory, depreactedActions, depreactedCommands, depreactedQueries } from '@ali/4ever-plugin-highlight';

const createHighlightPlugin = (configs: BiPluginConfig) => biFactory(configs);

createHighlightPlugin.actions = depreactedActions;
createHighlightPlugin.commands = depreactedCommands;
createHighlightPlugin.queries = depreactedQueries;

export default createHighlightPlugin;
